* {
  background-color: #000;
  color: #8f8;
  font-family:  'Abel', sans-serif;
  margin: 1px;
  padding: 1px;
  width: auto;
  height: auto;
}

.centering-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

#editor {
  border-top: 1px solid #8f8;
  border-right: 1px solid #0f0;
  border-bottom: 1px solid #0f0;
  border-left: 1px solid #8f8;
  margin: 30px auto 1px auto;
  width: 100vw;
  background-color: #101;
  color: #0f0;
  font-family: monospace;
  font-size: 13px;
  resize: none;
  white-space: pre;
}

#preview,
#preview * {
  overflow-x: auto;
}

li {
  list-style-position: inside;
}

#preview code {
  font-family: monospace;
  background-color: #101;
  color: #0f0;
}

#preview pre {
  margin-left: 20px;
  padding: 5px;
  background-color: #101;
  overflow-x: auto;
  border-radius: 5px;
}

#preview pre code {
  font-weight: 100;
  background-color: #101;
  color: #0f0;
}


blockquote {
  background-color: #101;
  border-left: 1px solid #0f0;
  border-radius: 5px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  margin-left: 20px;
}

blockquote * {
  background-color: transparent;
}

a {
  color: #5ff;
}

a:visited {
  color: #1bb;
}

hr {
  background-color: #111;
  border: 1px solid #111;
}

img {
  max-width: 1000px;
}


